



.box__field {
    margin: 0 auto;
    margin-top: 0.3em;
    padding: 0 .5em .5em .5em;
    width: 97%;
    border-radius: .5em;
    border: 1px solid $bor;
    // background: rgb(172, 172, 172);
    background-color: white;

    legend {
        padding: 0 .5em;
        color: rgb(192, 192, 192);
        font-size: .7em;
    }
}


.box {
    display: flex;
    flex-direction: column;

    &__name {
        padding: .65em;
        font-weight: 700;
        border: 0;
        border-bottom: 1px solid $bor;
        outline: 0;
        background: transparent;
        text-transform: uppercase;

        &::placeholder {
            opacity: .4;
        }
    }

    &__textarea {
        padding: .95em;
        border: 0;
        outline: 0;
        background: transparent;

        &::-webkit-scrollbar {
            background: transparent;
            width: .35em;
        }
    
        &::-webkit-scrollbar-thumb {
            background: rgb(54, 134, 240);
        }
    }

    &__bottom {
        margin-top: .2em;
        padding: .2em .3em;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: #f1f1f1;
        border-radius: .15em;
    }

    &__file {
        // display: none;
    }

    &__camera {
        cursor: pointer;
        transition: color .3s linear;


        &:hover {
            color: rgb(54, 134, 240);
        }
    }

    &__send {
        cursor: pointer;
        background: transparent;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0;
        outline: 0;
        width: 2.8em;
        height: 2.8em;
        background: rgb(54, 134, 240);
        border-radius: .5em;

        &__icon {
            font-size: 1.6em;
            color: white;
        }
    }
}

