


.message {
    padding: .5em .7em;
    width: 100%;
    height: 23em;
    // border-bottom: 1px solid rgb(226, 226, 226);

    overflow-y: scroll;
    &::-webkit-scrollbar {
        background: transparent;
        width: .35em;
    }

    &::-webkit-scrollbar-thumb {
        background: rgb(54, 134, 240);
    }
}   


.chatCard {
    position: relative;
    margin-bottom: 1em;
    padding: .5em .5em .3em .5em;
    background: #f5f5f5;
     border-radius: .5em;
     border-bottom-left-radius: 0;
     border: 1px solid $bor;
     width:fit-content;

     &:last-child {
        margin-bottom: 0;
     }

     &::after {
        content:"";
        position: absolute;
        left: -.02em;
        bottom: -.5em;
        clip-path: polygon(0 0, 0% 100%, 100% 0);
        background: #f5f5f5;
        width: 1em;
        height: 1em;
        border-left: 1px solid $bor;
     }

     &__header {
        margin-bottom: .5em;
        display: flex;
        // align-items: center;
        flex-direction: column;

        &__icon {
            display: flex;
            align-items: center;
            color: rgb(54, 134, 240);
            font-weight: 100!important;
        }
     }

     &__left {
        display: flex;
        align-items: center;
        margin-bottom: 0.3em;
     }

     &__name {
        margin: 0 1em 0 .15em;
        font-size: 1.1em;
        font-weight: 500;
        color: rgb(54, 134, 240);
     }

     &__hours {
        color: #ccc;
        font-size: .8em;
        font-weight: 100;
     }

     &__body {
        margin-bottom: .7em;
        font-size: .85em;
        font-weight: 100;

     }


     &__btns {
        padding: 1em 0 0 0;
        display: flex;
        align-items: center;
        border-top: .050em solid rgba(54, 135, 240, 0.234);
        // background: green;
     }

     &__btn {
         position: relative;
         margin-right: 2em;
         background: transparent;
         border: 0;
         outline: 0;
         cursor: pointer;

        &:last-child {
            margin-right: 0;
        }

        &__icon {
            font-size: 1.5em;
        }

        &__counter  {
            position: absolute;
            top: -1.5em;
            right: -1em;
            padding: 0.3em;
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 1.5em;
            min-height: 1.5em;
            border-radius: 50%;
            background:rgb(54, 134, 240);
            color: rgb(224, 224, 224);
        }
     }

     .delete {
        color: red;
     }
}