
@import url('https://fonts.googleapis.com/css2? family= Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700; 1 900 & display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap');


*,
*::after,
*::before { 
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

$bor  : rgb(107, 107, 107);

body {
    font-size: 1.27vw;
    font-family: 'Roboto', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    height: 100vh;
    background: rgb(127, 200, 222);
    background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)),url('../assets/bac1.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: scroll;
    background-position: center center;
    animation: routage 10s ease-in-out infinite;

    @keyframes routage {
        0%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        10%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        20%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        30%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        40%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        50%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac1.jpeg');}
        60%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac2.jpeg');}
        70%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac2.jpeg');}
        80%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac2.jpeg');}
        90%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac2.jpeg');}
        100%{ background-image: linear-gradient(rgba(0, 0, 0, 0.652),rgba(0, 0, 0, 0.733)), url('../assets/bac2.jpeg');}
    }

}

.container {
    position: relative;
    margin: 3em auto;
    margin-bottom: 0!important;
    border: 1px solid $bor;
    border-radius: .5em;
    width: 50em;
    height: 35em;
    background: rgba(0, 0, 0, 0.109);
}



