


.dashboard {
    padding: .5em 1em;
    border-right: 1px solid $bor;
    width: 16em;
    height: 100%;
}

.listD {
    display: flex;
    flex-direction: column;

    &__card {
        position: relative;
        margin-bottom: .75em;
        padding: .6em;
        padding-top: 1.85em;
        border-radius: 0.35em;
        border: 1px solid #4848481b;
        min-height: auto;

        &:nth-child(1) {
            background-color: #00c6fb;
        }

        &:nth-child(2) {
            background-color: #f9d423;
        }

        &:nth-child(3) {
            background-color: #0ba360 ;
        }

        &:nth-child(4) {
            background-color: #c1c161  ;
        }
    }

    &__numerote {
        position: absolute;
        top: .3em;
        left: .3em;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 1.4em;
        height: 1.4em;
        font-size: .9em;
        background: black;
        color: white;
        border-radius: 50%;
    }

    &__title {
        margin-bottom: .5em;
        font-size: .75em;
        font-weight: 600;
        text-transform: uppercase;
    }

    &__content {
        font-size: .75em;
        line-height: 120%;
    }
}