



.chat {
    // background: green;/
    width: 33.85em;

    &__header {
        padding: 0 .85em;
        display: flex;
        align-items: center;
        width: 100%;
        height: 4em;
        border-bottom: 1px solid $bor;
    }
    
    &__title {
        margin-right: 4em;
       font-family: 'Alfa Slab One', serif;
       font-size: 1.3em;
       color: white;

       > span {
          color: #14b4e1;
       }
    }

    &__right {
        position: relative;
    }

    &__input {
        padding: .8em 1em;
        border-radius: .5em;
        outline: 0;
        border: 0;
        font-size: .67em;
        border: 0.065em solid $bor;
        width: 19em;
        background: rgb(230, 230, 230);
    }

    &__icon {
        position: absolute;
        top: .45em;
        right: .3em;
        color: rgb(188, 188, 188);
    }

    &__container {
        display: flex;
        flex-direction: column;
    }
}